@import '../../assets/scss/app.scss';

.app {
  background: #020525;
  //height: 100vh;

  &_no_scroll {
    overflow: hidden;
    height: 100vh;
  }

  .swiper {
    @include onlyDesktop() {
      width: 100%;
      height: 100vh;

      .slide {
        display: flex;
        align-items: center;
        justify-content: center;

        p {
          font-size: 50px;
          color: red;
        }
      }
    }
  }

  .components {
    @include desktop() {
      display: none;
    }
  }

}